import axios from 'axios';
import Joi from 'joi';
import React, { useState } from 'react'
import { Link, useNavigate } from 'react-router-dom'

export default function Login(props) {
    const [errorList, setErrorList] = useState([]);
    let navigate = useNavigate();
    let apiUrl = 'https://routeegypt.herokuapp.com/'
    const [user, setUser] = useState({
        email: "", password: "",

    })
    const [error, setError] = useState('');
    function getUserData(e) {
        let movixUser = { ...user };
        movixUser[e.target.name] = e.target.value;

        setUser(movixUser);
    }
    async function submitRegForm(e) {
        e.preventDefault();
        let validateResult = validateRegForm();
        if (validateResult.error) {
            setErrorList(validateResult.error.details)

        }
        else {
            let { data } = await axios.post(apiUrl + 'signin', user)
            if (data.message === 'success') {
                localStorage.setItem('token', data.token)
                props.saveUserData();
                navigate('/home')

            }
            else {
                console.log(data)
                setError('Check Your Data')
            }
        }
    }
    function validateRegForm() {
        let scheme = Joi.object({
            email: Joi.string().email({ minDomainSegments: 2, tlds: { allow: ['com', 'net'] } }),
            password: Joi.string()
                .pattern(new RegExp('^[a-zA-Z0-9]{3,30}$')),
        });
        return scheme.validate(user, { abortEarly: false });
    }


















    return (
        <>
            <section className="login">
                <div className="container">
                    <div className="row">
                        <div className="col-md-12 mt-4">
                            <div className="box">
                                <div className="nav-log">
                                    <ul className="list-unstyled p-0 m-0 d-flex justify-content-center align-items-center">
                                        <li><Link className="active" to="/login">Login</Link></li>
                                        <li><Link to="/signup">Register</Link></li>
                                    </ul>
                                </div>
                                <form onSubmit={submitRegForm}>
                                    {/* {errorList.map((err, i) => i === 1 ? <div className="alert alert-warning d-flex">password incorect</div> : <div className="alert alert-warning d-flex">{err.message}</div>)} */}
                                    {error ? <div className="alert alert-warning d-flex">{error}</div> : ''}
                                    <div className="form-group">
                                        <input onChange={getUserData} className="form-control" type="email" name="email" placeholder="Email" />
                                    </div>
                                    <div className="form-group">
                                        <input onChange={getUserData} className="form-control" type="password" name="password" placeholder="Password" />
                                    </div>


                                    <button className="btn-login mt-3">Login</button>
                                </form>
                            </div>
                        </div>


                    </div>
                </div>
            </section>

        </>
    )
}
